$bodyBackgroundColor: #60829e;
$bodyColor: #000000;
$bodyFont: -apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
$innerBackgroundColor: #ffffff;
$footerColor: #ffffff;

body {
  background: $bodyBackgroundColor;
  box-sizing: border-box;
  color: $bodyColor;
  font-family: $bodyFont;
}

main {
  background: $innerBackgroundColor;
  margin: 0 auto;
  padding: 3rem 5.263rem;
  max-width: 880px; 
}

.emoji {
  vertical-align: text-bottom;
}

.site-info {
  color: $footerColor;
  text-align: center;
}

// ACCORDION (for Geocaches)

.accordionItem {
  border-bottom: 1px solid #ddd;

  h2 { 
    margin: 0; 
    font-size: 1.1em; 
    padding: 0.4em; 
  
    &:hover { 
      cursor: pointer; 
    }
  }

  div { 
    margin: 0; 
    padding: 1em 0.4em; 
  }

  &.hide {
    h2 { 
      color: #000; 
    }

    div { 
      display: none; 
    }
  }
}

// IMAGE GALLERIES

.gallery {
  display: flex;
  justify-content: space-around;
  flex-flow: row wrap;
  flex-basis: calc(100% / 4);

  @media (max-width: 768px) {
    flex-wrap: wrap;
  }

  div {
    flex: 1;
    flex-basis: 240px;
    margin: 0.263rem;    

    @media (max-width: 768px) {
      margin: 0 auto 0.5rem auto;
    }
  }

  img {
    height: auto;
    width: 100%;  
  }
}

// TABS

.invisible {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.example-tabs-tabs__list {
  margin: 0;
  padding: 0;
}
.example-tabs-tabs__item {
  display: inline-block;
}

.example-tabs-tabs__link {
  display: inline-block;
  padding: 1em  2em;
  background: #f2f2f2;
  cursor: pointer;
  color: #148297;
}

[aria-selected="true"].example-tabs-tabs__link {
  color: #000;
}

.example-tabs-tabs__content {
  background: #f2f2f2;
  padding: 1em;
}

[aria-hidden="true"].example-tabs-tabs__content {
  display: none;
}
